.content {
    margin-left: auto; margin-right: auto;
    width: 90%; padding: 20px; border-radius: 20px; margin-top: 20px; margin-bottom: 40px;
    background: white; font-Size: 15px;}
.head {
    display: flex;
}
.date {
    display: flex; width: 15%; justify-content: center; align-items: center;
    border-radius: 10px; padding: 5px; margin-right: 20px;
    color: #fcc9ee; border: medium solid #fcc9ee; background-clip: padding-box;
    font-size: 20px; font-weight: bold;
}
.summary{
    display: flex; width: 85%; align-items: center; padding-left: 10px;
    border: medium solid #fcc9ee; background-clip: padding-box; border-radius: 10px;
}
.hr {
    background-color: #fcc9ee; height: 2px; 
}
.ex {
    padding: 20px; border-radius: 10px;
    border: medium solid #fcc9ee; background-clip: padding-box;
}
.input {
    width: 40%; margin-Left: 10px;
    font-Size: 20px;
}
.button {
    width: 80px; margin-Left: 10px; margin-Top: 10px;
    font-Size: 15px;
}
.table {
    margin-left: auto; margin-right: auto; margin-top: 20px;
    border: 3px solid #fcc9ee; border-radius: 10px;
}
.th{
    border: 2px solid #fcc9ee; border-radius: 8px; font-size: 15px; padding: 5px;
}
.td {
    border: 2px dotted #fcc9ee; border-radius: 8px; font-size: 14px; padding: 3px;
}
.block {
    margin-left: 40px; font-size: 14px; margin-top: 10px; margin-bottom: 10px;
}